// Fonts
@font-face {
    font-family: 'Candara';
    src: url('/fonts/Candara/Candara.eot');
    src: url('/fonts/Candara/Candara.eot?#iefix') format('embedded-opentype'),
    url('/fonts/Candara/Candara.svg#Candara') format('svg'),
    url('/fonts/Candara/Candara.ttf') format('truetype'),
    url('/fonts/Candara/Candara.woff') format('woff'),
    url('/fonts/Candara/Candara.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Candara-Bold';
    src: url('/fonts/Candara/Candara-Bold.eot');
    src: url('/fonts/Candara/Candara-Bold.eot?#iefix') format('embedded-opentype'),
    url('/fonts/Candara/Candara-Bold.svg#Candara-Bold') format('svg'),
    url('/fonts/Candara/Candara-Bold.ttf') format('truetype'),
    url('/fonts/Candara/Candara-Bold.woff') format('woff'),
    url('/fonts/Candara/Candara-Bold.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

// Variables
:root {
    --landing-color-font-promary: #ffffff;
    --landing-color-font-required: #ff0000;
    --landing-color-link: #0085ff;
    --landing-color-background-primary: #000000;
    --landing-color-border: rgba(45, 129, 74, 0.8);
    --landing-color-back-blur: rgba(9, 61, 20, 0.4);
    --landing-back-blur-value: 15px;
    --landing-color-button: rgba(185, 179, 156, 0.8);
    --landing-color-button-hover: #00944d;
    --landing-button-blur-value: 20px;
    --landing-input-back: rgba(255, 255, 255, 0.3);
    --landing-input-border: rgba(255, 255, 255, 0.7);
    --landing-input-border-valid: #00e075;
    --landing-input-border-error: #ff003d;
    --landing-checkbox-border: rgba(77, 237, 160, 0.8);
}

// Default styles
*,
*:before,
*:after {
    box-sizing: border-box;
    outline: none !important;
}

html {
    -webkit-text-size-adjust: none;
    touch-action: manipulation;
}

// Удаление иконки очистки поля в IE
input[type="text"]::-ms-clear {
    display: none;
}

// Удаление иконки отображения пароля в IE
input[type="password"]::-ms-reveal {
    display: none;
}

body, html {
    min-height: 100vh;
}

body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    font-family: 'Candara', 'sans-serif';
    font-weight: normal;
    font-style: normal;
    color: var(--landing-color-font-promary);
    background-color: var(--landing-color-background-primary);
    background-image: url("https://9d2c73b6-1b6d-45e9-9faa-7fb794647734.selcdn.net/i/590/1ffc226d8e.jpg");
    background-position: center top;
    background-repeat: repeat-y;
    background-size: contain;

    @media (min-width: 1200px) {
        background-size: initial;
    }
}

.hidden {
    display: none !important;
}

.invisible {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
    z-index: -1;
}

ul, li {
    list-style: none;
    margin: 0;
    padding: 0;
}

.container {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding-right: 18px;
    padding-left: 18px;

    @media (min-width: 768px) {
        width: 676px;
        padding-right: 0;
        padding-left: 0;
    }

    @media (min-width: 992px) {
        width: 970px;
    }

    @media (min-width: 1400px) {
        width: 1360px;
    }
}

.decorated {
    border-radius: 30px;
    backdrop-filter: blur(var(--landing-back-blur-value));
    background-color: var(--landing-color-back-blur);
    border: 2px solid var(--landing-color-border);

    @media (min-width: 768px) {
        border-radius: 40px;
    }
}

.d-none {
    display: none;
}

.d-xl-block {
    @media (min-width: 1400px) {
        display: block;
    }
}

.landing {
    &__section {
        position: relative;

        &_caption {
            margin-bottom: 283px;

            @media (min-width: 768px) {
                margin-bottom: 526px;
            }

            @media (min-width: 1400px) {
                margin-bottom: 617px;
            }
        }

        &_cards {
            margin-bottom: 256px;

            @media (min-width: 768px) {
                margin-bottom: 517px;
            }

            @media (min-width: 1400px) {
                margin-bottom: 854px;
            }
        }

        &_benefits {
            margin-bottom: 278px;

            @media (min-width: 768px) {
                margin-bottom: 556px;
            }

            @media (min-width: 1400px) {
                margin-bottom: 1031px;
            }
        }

        &_slider {
            margin-bottom: 64px;

            @media (min-width: 768px) {
                margin-bottom: 100px;
            }

            @media (min-width: 1400px) {
                margin-bottom: 160px;
            }
        }

        &_form {
