        cursor: pointer;

        @media (min-width: 768px) {
            padding: 0 32px;
            height: 56px;
            font-size: 21px;
            line-height: 26px;
        }

        @media (min-width: 1400px) {
            padding: 0 40px 2px;
            height: 55px;
            font-size: 25px;
            line-height: 25px;

