                line-height: 33px;
            }

            @media (min-width: 1400px) {
                font-size: 31px;
                line-height: 38px;
            }
        }

        &-back {
            display: block;
            width: 100%;
            max-width: 324px;
            height: 393.564697265625px;
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate(-50%, 24px);
            background: url("https://9d2c73b6-1b6d-45e9-9faa-7fb794647734.selcdn.net/i/505/a8c307ee3a.png") center no-repeat;
            background-size: cover;
            z-index: -1;

