                max-width: 677px;
                height: 700px;
                transform: translate(-50%, 25px);
                background-position-y: bottom;
            }

            @media (min-width: 1400px) {
                max-width: 1360px;
                height: 985px;
                transform: translate(-50%, -140px);
            }
        }
    }

    &__cards {
        padding: 38px 20px 46px 20px;

        @media (min-width: 768px) {
            padding: 46px 4px 66px 0;
        }

        @media (min-width: 1400px) {
            padding: 78px 0 78px 0;
        }

        &-list {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            gap: 40px;

            @media (min-width: 768px) {
                column-gap: 25px;
            }

            @media (min-width: 1400px) {
                gap: 40px;
            }
        }

        &-item {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 228px;
            border-bottom: 1px solid var(--landing-color-font-promary);

            @media (min-width: 768px) {
                width: 275px;
            }

            @media (min-width: 1400px) {
                width: 360px;
            }

            .img {
                display: block;
                width: 228px;
                height: 228px;

                @media (min-width: 768px) {
                    width: 275px;
                    height: 275px;
                }

                @media (min-width: 1400px) {
                    width: 360px;
                    height: 360px;
                }

                img {
                    display: block;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            .txt {
                display: block;
                margin: 10px 0;
                font-family: 'Candara-Bold', sans-serif;
                font-weight: 400;
                font-size: 18px;
                line-height: 22px;
                text-align: center;
                text-transform: uppercase;

                @media (min-width: 768px) {
                    margin: 14px 0 12px;
                    font-size: 21px;
