                }

                @media (min-width: 1400px) {
                    margin: 17px 0 15px;
                    font-size: 25px;
                    line-height: 31px;
                }
            }
        }

        &-button {
            width: 100%;
            height: auto;
            display: flex;
            justify-content: center;
            margin-top: 39px;

            @media (min-width: 768px) {
                margin-top: 40px;
            }

            @media (min-width: 1400px) {
                margin-top: 60px;
            }
        }

        &-back {
            display: block;
            width: 100%;
            max-width: 323.82px;
            height: 325.72px;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 192px);
            background-image: url("https://9d2c73b6-1b6d-45e9-9faa-7fb794647734.selcdn.net/i/509/46abaa76b9.png");
            background-size: cover;
            background-position: center bottom;
            background-repeat: no-repeat;
            z-index: -1;

            @media (min-width: 768px) {
                max-width: 674px;
                height: 677.96px;
                transform: translate(-50%, 418px);

            }

            @media (min-width: 1400px) {
                max-width: 1360px;
                height: 1368px;
                transform: translate(-50%, 694px);
            }
        }
    }

    &__benefits {
        padding: 32px 0 48px;

        @media (min-width: 768px) {
            padding: 46px 0 66px 0;
        }

        @media (min-width: 1400px) {
            padding: 79px 0 78px 0;
        }

        &-list {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            gap: 20px;

            @media (min-width: 768px) {
                gap: 32px;
            }

            @media (min-width: 1400px) {
                gap: 40px;
            }
        }

        &-item {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 276px;

            @media (min-width: 768px) {
                width: 271.5px;
            }

            @media (min-width: 1400px) {
                width: 361.67px;
            }

            .img {
                display: block;
                width: 48px;
                height: 48px;

                @media (min-width: 768px) {
                    width: 64px;
                    height: 64px;
                }

                @media (min-width: 1400px) {
                    width: 96px;
                    height: 96px;
                }

                img {
                    display: block;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            .txt {
                display: block;
                margin: 20px 0 12px;
                font-size: 18px;
                line-height: 22px;
                text-align: center;

                @media (min-width: 768px) {
                    margin: 24px 0 12px;
                    font-size: 21px;
                    line-height: 26px;
                }

                @media (min-width: 1400px) {
                    margin: 27px 0 13px;
                    font-size: 25px;
                    line-height: 31px;
                }
            }
        }

        &-button {
            width: 100%;
            height: auto;
            display: flex;
            justify-content: center;
            margin-top: 20px;

            @media (min-width: 768px) {
                margin-top: 60px;
            }
        }

        &-back {
            display: block;
            width: 100%;
            max-width: 324px;
            height: 346.87px;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 214px);
            background-image: url("https://9d2c73b6-1b6d-45e9-9faa-7fb794647734.selcdn.net/i/516/4468a6bd25.png");
            background-size: cover;
            background-position: center bottom;
            background-repeat: no-repeat;
            z-index: -1;

            @media (min-width: 768px) {
                max-width: 675px;
                height: 722px;
                transform: translate(-50%, 451px);
            }

            @media (min-width: 1400px) {
                max-width: 1360px;
                height: 1456px;
                transform: translate(-50%, 871px);
            }
        }
    }

    &__slider {
        padding: 19px 0 46px;

        @media (min-width: 768px) {
            padding: 41px 0 66px 0;
        }

        @media (min-width: 1400px) {
            padding: 73px 0 78px 0;
        }

        .swiper {
            width: 100%;
            height: 100%;
        }

        .swiper-slide {
            display: flex;
            justify-content: center;
            align-items: center;

            img {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &-title {
            display: block;
            position: relative;
            margin: 0;
            padding: 0 25px;
            font-family: 'Candara', 'sans-serif';
            font-weight: normal;
            font-style: normal;
            font-size: 28px;
            line-height: 35px;
            text-align: center;

            @media (min-width: 768px) {
                padding: 0 50px;
                font-size: 42px;
                line-height: 52px;
            }

            @media (min-width: 1400px) {
                font-size: 48px;
                line-height: 60px;
            }
        }

        &-text {
            display: block;
            position: relative;
            margin: 11px 0 22px;
            padding: 0 25px;
            font-size: 18px;
            line-height: 22px;
            text-align: center;

            @media (min-width: 768px) {
                margin: 12px 0 43px;
                padding: 0 50px;
                font-size: 27px;
                line-height: 33px;
            }

            @media (min-width: 1400px) {
                margin: 21px 0 64px;
                font-size: 31px;
                line-height: 38px;
            }
        }

        &-body {
            display: block;
            position: relative;
            margin: 0 auto;
            width: 251px;
            height: 142.37px;

            @media (min-width: 768px) {
                width: 523px;
                height: 299.96px;
            }

            @media (min-width: 1400px) {
                width: 1040px;
                height: 595.42px;
            }
        }

        &-controller {
            display: block;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            width: 24px;
            height: 24px;
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            z-index: 1;
            cursor: pointer;
            transition: all .1s linear;

           @media (min-width: 768px) {
               width: 64px;
               height: 64px;
           }

            @media (min-width: 1400px) {
                width: 96px;
                height: 96px;
            }

            &.swiper-button-disabled {
                cursor: default;
                opacity: 0.3;
            }
        }

        &-controller_prev {
            left: 10px;
            background-image: url("data:image/svg+xml;base64,");

            @media (min-width: 360px) {
                left: -30px;
            }

            @media (min-width: 768px) {
                left: -70px;
            }

            @media (min-width: 1400px) {
                left: -126px;
            }
        }

        &-controller_next {
            right: 10px;
            background-image: url("data:image/svg+xml;base64,");

            @media (min-width: 360px) {
                right: -30px;
            }

            @media (min-width: 768px) {
                right: -70px;
            }

            @media (min-width: 1400px) {
                right: -126px;
            }
        }

        &-button {
            width: 100%;
            height: auto;
            display: flex;
            justify-content: center;
            margin-top: 40px;

            @media (min-width: 768px) {
                margin-top: 60px;
            }
        }
    }

    &__form {
        padding: 20px 22px 46px;

        @media (max-width: 767px) {
            border-radius: 20px;
        }

        @media (min-width: 768px) {
            padding: 43px 50px 65px 46px;
        }

        @media (min-width: 1400px) {
            padding: 74px 98px 78px 98px;
        }

        &-title {
            display: block;
            position: relative;
            margin: 0;
            padding: 0;
            font-family: 'Candara', 'sans-serif';
            font-weight: normal;
            font-style: normal;
            font-size: 28px;
            line-height: 35px;
            text-align: center;

            @media (min-width: 768px) {
                font-size: 42px;
                line-height: 52px;
            }

            @media (min-width: 1400px) {
                font-size: 48px;
                line-height: 60px;
            }
        }

        &-body {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            margin-top: 12px;

            @media (min-width: 768px) {
                margin-top: 25px;
            }

            @media (min-width: 1400px) {
                margin-top: 45px;
            }
        }

        &-group {
            display: flex;
            flex-direction: column;

            @media (min-width: 1400px) {
                flex-direction: row;
                justify-content: space-between;
                column-gap: 40px;
            }
        }

        &-label {
            display: block;
            position: relative;
            width: 100%;
            height: 40px;
            margin: 0 0 58px 0;
            padding: 0;
            background-color: var(--landing-input-back);
            backdrop-filter: blur(var(--landing-button-blur-value));
            border-radius: 4px;

            @media (min-width: 768px) {
                height: 48px;
                margin-bottom: 61px;
            }

            @media (min-width: 1400px) {
                height: 56px;
                margin-bottom: 65px;
            }

            &_text {
                height: auto !important;
                margin-top: 18px;
                margin-bottom: 52px;
                background-color: transparent;
                backdrop-filter: none;

                @media (min-width: 768px) {
                    margin-top: 0;
                    margin-bottom: 55px;
                }

                @media (min-width: 1400px) {
                    margin-top: 25px;
                    margin-bottom: 59px;
                }

                textarea {
                    background-color: var(--landing-input-back) !important;
                    backdrop-filter: blur(var(--landing-button-blur-value));
                }

                span {
                    padding-left: 55px;
                    transform: translateY(44px) !important;

                    @media (min-width: 1400px) {
                        padding-left: 80px;
                    }
                }

                cite {
                    transform: translateY(44px) !important;
                }
            }

            &::after {
                content: '';
                display: block;
                position: absolute;
                top: 50%;
                right: 8px;
                transform: translateY(-50%);
                width: 24px;
                height: 24px;
                opacity: 0;
                background-position: center;
                background-repeat: no-repeat;
                background-size: cover;
                background-image: url("data:image/svg+xml;base64,");
                transition: opacity .1s linear;

                @media (min-width: 768px) {
                    width: 32px;
                    height: 32px;
                }

                @media (min-width: 1400px) {
                    width: 42px;
                    height: 42px;
                    right: 7px;
                }
            }

            input, .input, textarea {
                opacity: 1;
                transform: none;
                bottom: initial;
                z-index: 0;
                top: 0;
                left: 0;
                width: 100%;
                margin: 0;
                background-color: transparent;
                border: 1px solid var(--landing-input-border);
                border-radius: 4px;
                font-family: 'Candara', 'sans-serif';
                font-weight: normal;
                font-style: normal;
                font-size: 18px;
                color: var(--landing-color-font-promary);
                font-variant: small-caps;
                transition: all .1s linear;
                cursor: pointer;

                @media (min-width: 768px) {
                    padding-left: 15px !important;
                    font-size: 21px;
                }

                @media (min-width: 1400px) {
                    font-size: 25px;
                    padding-left: 19px !important;
                    padding-top: 0 !important;
                    padding-bottom: 1px !important;
                }
            }

            input, .input {
                display: block;
                height: 100%;
                padding: 1px 40px 0 14px;
                line-height: 40px;
                outline: 0;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;

                @media (min-width: 768px) {
                    line-height: 48px;
                }

                @media (min-width: 1400px) {
                    line-height: 56px;
                }
            }

            textarea {
                height: 118px;
                padding: 9px 13px;
                resize: none;
                line-height: 22px;

                @media (min-width: 768px) {
                    height: 137px;
                    padding: 9px 13px;
                    line-height: 26px;
                }

                @media (min-width: 1400px) {
                    height: 157px;
                    padding-top: 11px !important;
                    line-height: 31px;
                }
            }

            mark {
                display: flex;
                align-items: center;
                position: absolute;
                z-index: 1;
                top: 0;
                left: 0;
                width: 100%;
                height: 40px;
                margin: 0;
                padding: 1px 40px 0 14px;
                font-family: 'Candara', 'sans-serif';
                font-weight: normal;
                font-style: normal;
                font-size: 18px;
                line-height: 22px;
                color: var(--landing-color-font-promary);
                font-variant: small-caps;
                background: transparent;
                cursor: pointer;

                @media (min-width: 768px) {
                    height: 48px;
                    padding-left: 16px;
                    font-size: 21px;
                    line-height: 26px;
                }

                @media (min-width: 1400px) {
                    height: 56px;
                    font-size: 25px;
                    line-height: 31px;
                    padding-left: 20px;
                    padding-top: 0;
                    padding-bottom: 1px;
                }

                &.required {
                    &::after {
                        content: '';
                        display: inline-block;
                        width: 4px;
                        height: 4px;
                        background-color: var(--landing-color-font-required);
                        transform: translate(4px, -4px);
                        border-radius: 50%;

                        @media (min-width: 1400px) {
                            transform: translate(5px,-11px);
                        }
                    }
                }
            }

            span, cite {
                display: flex;
                position: absolute;
                z-index: -1;
                bottom: 0;
                transform: translateY(48px);
                left: 0;
                width: 100%;
                height: 40px;
                font-style: normal;
                font-size: 15px;
                line-height: 18px;
                opacity: 0;
                transition: opacity .1s linear;

                @media (min-width: 768px) {
                    font-size: 17px;
                    line-height: 21px;
                }

                @media (min-width: 1400px) {
                    font-size: 20px;
                    line-height: 25px;
                }
            }

            var {
                font-style: normal;
            }

            cite {
                opacity: 1 !important;
            }

            button {
                display: flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                top: 1px;
                right: 1px;
                z-index: -1;
                opacity: 0;
                transition: all .1s linear;
                width: 40px;
                height: calc(100% - 2px);
                background-color: transparent;
                border: none;
                border-top-right-radius: 4px;
                border-bottom-right-radius: 4px;
                cursor: pointer;

                @media (min-width: 768px) {
                    width: 50px;
                }

                @media (min-width: 1400px) {
                    width: 59px;
                }

                &::before {
                    content: '';
                    display: block;
                    width: 24px;
                    height: 24px;
                    background-position: center;
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-image: url("data:image/svg+xml;base64,");

                    @media (min-width: 768px) {
                        width: 32px;
                        height: 32px;
                    }

                    @media (min-width: 1400px) {
                        width: 42px;
                        height: 42px;
                    }
                }
            }

            &.loading {
                &::before {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 1px;
                    left: 1px;
                    width: calc(100% - 40px);
                    height: calc(100% - 2px);
                    border-top-left-radius: 4px;
                    border-bottom-left-radius: 4px;
                    z-index: 0;
                    background-repeat: no-repeat;
                    background-position: -140px center;
                    background-image: url("data:image/svg+xml;base64,");
                    animation: loading-file 0.5s linear infinite;

                    @media (min-width: 768px) {
                        width: calc(100% - 50px);
                    }

                    @media (min-width: 1400px) {
                        width: calc(100% - 60px);
                    }
                }
            }

            &.filled {
                mark {
                    display: none;
                }

                button {
                    opacity: 1;
                    z-index: 1;
                }
            }

            &.valid {
                &::after { opacity: 1; }

                input, .input {
                    border-color: var(--landing-input-border-valid);
                }
            }

            &.error {
                input, .input, textarea {
                    border-color: var(--landing-input-border-error);
                }

                span { opacity: 1; }
            }
        }

        &-comment {
            position: relative;
            padding-left: 8px;
            font-size: 15px;
            line-height: 18px;
            text-align: left;

            @media (min-width: 768px) {
                font-size: 17px;
                line-height: 21px;
            }

            @media (min-width: 1400px) {
                font-size: 20px;
                line-height: 25px;
            }

            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 2px;
                left: 0;
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background-color: var(--landing-color-font-required);
            }
        }

        &-checkbox {
            display: block;
            position: relative;
            margin-top: 20px;
            padding-left: 36px;
            font-size: 15px;
            line-height: 18px;

            @media (min-width: 768px) {
                margin-top: 40px;
                padding-left: 48px;
                font-size: 17px;
                line-height: 21px;
            }

            @media (min-width: 1400px) {
                margin-top: 52px;
                padding-left: 69px;
                font-size: 20px;
                line-height: 25px;
            }

            a {
                font-size: inherit;
                line-height: inherit;
                color: var(--landing-color-link);
                text-decoration: underline;

                @media (min-width: 1400px) {
                   &:hover {
                       text-decoration: none;
                   }
                }
            }

            input:checked + .checker::before {
                transform: scale(1);
                opacity: 1;
            }

            .checker {
                display: block;
                position: absolute;
                top: 2px;
                left: 0;
                width: 24px;
                height: 24px;
                border: 1px solid var(--landing-checkbox-border);
                backdrop-filter: blur(var(--landing-button-blur-value));
                border-radius: 4px;
                background-color: var(--landing-input-back);
                cursor: pointer;

                @media (min-width: 768px) {
                    width: 32px;
                    height: 32px;
                }

                @media (min-width: 1400px) {
                    top: -10px;
                    width: 48px;
                    height: 48px;
                }

                &::before {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    opacity: 0;
                    transform: scale(0.8);
                    transition: all .1s linear;
                    background-position: center;
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-image: url("data:image/svg+xml;base64,");
                }
            }
        }

        &-button {
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            position: relative;
            margin-top: 32px;

            @media (min-width: 768px) {
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                margin-top: 58px;
            }

            @media (min-width: 1400px) {
                align-items: flex-end;
                margin-top: 74px;
            }
        }

        &-recaptcha {
            display: block;
            height: 56px;
            width: 56px;
            margin-top: 30px;

            @media (min-width: 768px) {
                position: absolute;
                right: 0;
                bottom: 1px;
                width: 72px;
                height: 72px;
                margin: 0;
            }

            @media (min-width: 1400px) {
                width: 96px;
                height: 96px;
            }
        }
    }

    &__footer {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        width: 100%;
        height: 40px;
        margin: 0 0 40px 0;
        padding: 0;
        background-color: var(--landing-color-background-primary);

        @media (min-width: 768px) {
            height: 80px;
            margin-bottom: 80px;
        }

        @media (min-width: 1400px) {
            height: 100px;
            margin-bottom: 100px;
        }
    }

    &__socials {
        display: flex;
        justify-content: center;
        align-items: center;
        column-gap: 43px;

        @media (min-width: 768px) {
           column-gap: 84px;
        }

        @media (min-width: 1400px) {
           column-gap: 71px;
        }

        &-link {
            display: block;
            position: relative;
            width: 27.5px;
            height: 27.5px;
            background: var(--landing-color-button);
            backdrop-filter: blur(var(--landing-button-blur-value));
            border-radius: 10.619px;
            text-decoration: none;
            transition: all .1s linear;
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;

            @media (min-width: 768px) {
                width: 45.83332824707031px;
                height: 45.83332824707031px;
            }

            @media (min-width: 1400px) {
                width: 56.83333206176758px;
                height: 56.83333206176758px;

                &:hover {
                    background-color: var(--landing-color-button-hover);
                }
            }

            &_vk {
                background-image: url("data:image/svg+xml;base64,");
            }

            &_telegram {
                background-image: url("data:image/svg+xml;base64,");
            }

            &_ok {
                background-image: url("data:image/svg+xml;base64,");
            }
        }
    }

    @keyframes loading-file {
        0% {
            background-position-x: -25px;
        }
        100% {
            background-position-x: 0;
        }
    }
}
